import { InjectionToken } from '@angular/core';
export interface DTGAuthModuleOptions {
  apiUrl: string;
  loginUrl?: string;
  logUrl?: string;
  layerInfoUrl?: string;
  isAccessUrl?: string;
  appInfoUrl?: string;
  guard?: {
    simple?: {
      canActive?: (route, state) => void,
      canNotActive?: (route, state) => void,
      /**
       * Url mặc định khi canActive = false
       */
      defaultNavigateUrl?: string;
    }
  };
}
export const DTGAUTH_CONFIG = new InjectionToken<DTGAuthModuleOptions>('DTGAuthModuleOptions');

