import { Injectable, Inject } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { DTGAuthService } from '../ditagis-auth.service';
import { DTGAUTH_CONFIG, DTGAuthModuleOptions } from '../../public-api';

@Injectable({ providedIn: 'root', deps: [DTGAuthService] })
export class ErrorInterceptor implements HttpInterceptor {
    constructor(
        private authService: DTGAuthService,
        @Inject(DTGAUTH_CONFIG) private config: DTGAuthModuleOptions
    ) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(request).pipe(catchError(err => {
            const url = this.config && this.config.loginUrl || 'login';
            if (request.url.search(url) === -1) {
                if (err.status === 401) {
                    // auto logout if 401 response returned from api
                    this.authService.deAuthenticate();
                    location.reload(true);
                }
            }
            const error = err.error.message || err.statusText || 'Có lỗi xảy ra';
            return throwError(error);
        }));
    }
}
