import { Injectable, Inject } from '@angular/core';
import { DTGAUTH_CONFIG, DTGAuthModuleOptions } from '../dtg-auth.config';
import { LogEntity } from '../interfaces';
import { HttpClient } from '@angular/common/http';
import { DTGAuthService } from '../ditagis-auth.service';

@Injectable({ providedIn: 'root' })
export class DTGLogService {
  constructor(
    private http: HttpClient,
    private authService: DTGAuthService,
    @Inject(DTGAUTH_CONFIG) private config: DTGAuthModuleOptions
  ) {

  }

  create(entity: LogEntity) {
    const url = this.config.logUrl || `${this.config.apiUrl}/sys/logger`;
    return this.http.post<LogEntity>(url, JSON.stringify(entity), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: 'Bearer ' + this.authService.getToken()
      }
    });
  }
}
