import { UserEntity } from './user.interface';
import { ApplicationEntity } from './application.interface';
import { LayerEntity } from './layer.interface';

export interface LogEntity {
    id?: string;
    userId?: string;
    user?: UserEntity;
    createDate?: Date;
    applicationId?: string;
    application?: ApplicationEntity;
    actionTypeId?: LoggerActionTypeEnum;
    description?: string;
    note?: string;
    tableName?: string;
    table?: LayerEntity;
    objectId?: number;
}

export enum LoggerActionTypeEnum {
    INSERT = 'INSERT',
    LOGIN = 'LOGIN',
    LOGOUT = 'LOGOUT',
    REMOVE = 'REMOVE',
    UPDATE = 'UPDATE'
}
