import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DTGAUTH_CONFIG, DTGAuthModuleOptions } from '../dtg-auth.config';
import { Observable } from 'rxjs';
import { LayerEntity, LayerInfo } from '../interfaces';
import { DTGAuthService } from '../ditagis-auth.service';

@Injectable({ providedIn: 'root' })
export class DTGLayerInfoService {
  constructor(
    private http: HttpClient,
    private authService: DTGAuthService,
    @Inject(DTGAUTH_CONFIG) private config: DTGAuthModuleOptions
  ) { }

  get(): Observable<LayerInfo[]> {
    const url = this.config.layerInfoUrl ||
      `${this.config.apiUrl}/auth/layerinfos`;
    return this.http.get<LayerInfo[]>(url, {
      headers: {
        Authorization: 'Bearer ' + this.authService.getToken()
      }
    });
  }

  getGuest(): Observable<LayerEntity[]> {
    const url = this.config.layerInfoUrl ||
      `${this.config.apiUrl}/auth/layerinfos/anonymous`;
    return this.http.get<LayerEntity[]>(url);
  }

}
