import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ResponseLogin } from './interfaces/auth.interface';
import { DTGAUTH_CONFIG, DTGAuthModuleOptions } from './dtg-auth.config';
import { ApplicationEntity } from './interfaces';
import { Observable } from 'rxjs';

const TOKEN = 'token';
const USER = 'user';

@Injectable({ providedIn: 'root' })
export class DTGAuthService {
    constructor(
        private http: HttpClient,
        @Inject(DTGAUTH_CONFIG) private config: DTGAuthModuleOptions
    ) {

    }

    async authenticate(params: {
        username: string, password: string,
        appId?: string
    }) {
        const { username, password, appId } = params;
        const url = this.config.loginUrl || this.config.apiUrl + '/auth/login';
        const result = await this.http.post<ResponseLogin>(url, {
            username, password,
            appId
        }).toPromise();
        if (result) {
            localStorage.setItem(TOKEN, result.accessToken);
            localStorage.setItem(USER, JSON.stringify(result));
        }
        return result;
    }

    isAuthenticate(): boolean {
        return Boolean(this.getToken());
    }

    getToken() {
        return localStorage.getItem(TOKEN);
    }

    getInfo() {
        const user = localStorage.getItem(USER);
        if (user) {
            return JSON.parse(user);
        }
        return null;
    }

    deAuthenticate() {
        localStorage.removeItem(TOKEN);
        localStorage.removeItem(USER);
    }

    isAccess(params: {
        appId: string
    }) {
        const { appId } = params;
        const url = this.config.isAccessUrl ||
            `${this.config.apiUrl}/auth/isaccess/${appId}`;
        return this.http.get<boolean>(url, {
            headers: {
                Authorization: 'Bearer ' + this.getToken()
            }
        });
    }

    getAppInfo(params: {
        appId: string,
        isGuest?: boolean
    }): Observable<ApplicationEntity> {
        // Get a token from api server using the fetch api
        const { appId, isGuest } = params;
        let url = this.config.appInfoUrl || `${this.config.apiUrl}/auth/appinfo/${appId}`;
        let headers = new HttpHeaders();
        if (!isGuest && this.isAuthenticate()) {
            headers = headers.append('Authorization', 'Bearer ' + this.getToken());

        } else {
            url = url + '/anonymous';
        }
        return this.http.get<ApplicationEntity>(url, {
            headers
        });
    }
}
