import { NgModule, ModuleWithProviders } from '@angular/core';
import { DTGAuthModuleOptions, DTGAUTH_CONFIG } from './dtg-auth.config';
import { DTGAuthService } from './ditagis-auth.service';
import { DTGLogService } from './services/dtg-log.service';
import { HttpClientModule } from '@angular/common/http';
import { DTGLayerInfoService } from './services/dtg-layerinfo.service';
import { DTGSimpleGuard } from './guards/simple.guard';

@NgModule({
  declarations: [],
  imports: [
    HttpClientModule
  ]
})
export class DitagisAuthModule {
  static forRoot(options: DTGAuthModuleOptions): ModuleWithProviders<DitagisAuthModule> {
    return {
      ngModule: DitagisAuthModule,
      providers: [
        { provide: DTGAUTH_CONFIG, useValue: options },
        DTGAuthService, DTGLogService, DTGLayerInfoService, DTGSimpleGuard
      ]
    };
  }
}
